.t-main__video {
  position: relative;
}

.t-video__container {
  position: relative;
  width: rem(1170px);
  height: rem(760px);
  // transform: translateX(-32%);

  .t-video__preview {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(../images/t-video__preview.jpg);
    background-size: contain;
    background-repeat: no-repeat;
    filter: grayscale(100%);
  }

  .t-video__player {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    filter: none;

    &:hover {
      cursor: pointer;
    }
  }

  &.active:hover .t-video__pause {
    display: block;
  }
}

.t-video__play {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  border: none;
  background-color: transparent;
  transform: translate(-50%, -50%);

  .t-play__icon {
    display: block;
    width: rem(37px);
    height: rem(42px);
    margin-bottom: rem(40px);
    background-image: url(../images/t-video__play.svg);
    background-size: contain;
    background-repeat: no-repeat;
  }

  .t-play__descr {
    color: #fff;
    font-size: rem(16px);
    line-height: rem(22px);
  }

  &:hover {
    cursor: pointer;

    .t-play__icon {
      background-image: url(../images/t-video__play-h.svg);
    }

    .t-play__descr {
      color: #8e9295;
    }
  }
}

.t-video__pause {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  width: rem(24px);
  height: rem(38px);
  border: none;
  background-color: transparent;
  background-image: url(../images/t-video__pause.svg);
  transform: translate(-50%, -50%);

  &:hover {
    cursor: pointer;
    background-image: url(../images/t-video__pause-h.svg);
  }
}

.t-video__sound {
  display: none;
  position: absolute;
  bottom: rem(64px);
  left: rem(200px);
  z-index: 3;
  width: rem(34px);
  height: rem(34px);
  border: none;
  background-color: transparent;
  background-image: url(../images/t-video__sound.png);

  &:hover {
    cursor: pointer;
    background-image: url(../images/t-video__sound_hover.png);
  }
}

.t-video__sound.muted {
  width: rem(21px);
  background-image: url(../images/t-video__sound_muted.png);

  &:hover {
      background-image: url(../images/t-video__sound_muted_hover.png);
  }
}

.t-video__descr {
  position: absolute;
  top: 0;
  right: rem(-50px);
  width: rem(500px);
  padding-top: rem(108px);

  span {
    color: #105493;
    font-family: 'Proxima';
    font-size: rem(42px);
  }

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: rem(210px);
    height: rem(56px);
    margin-top: rem(80px);
    box-shadow: 0 rem(12px) rem(13px) rgba(0, 168, 178, 0.13);
    border-radius: rem(28px);
    background-color: #00a8b2;
    color: #ffffff;
    font-family: 'Proxima';
    font-size: rem(12px);
    font-weight: 900;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: rem(1.8px);
    transition: background-color cubic-bezier(0.39, 0.575, 0.565, 1) 0.15s;
    will-change: background-color;

    &:hover {
      color: #fff;
      background-color: #105493;
      text-decoration: none;
    }
  }
}