.t-header__presentation {
  display: flex;
  margin-top: rem(90px);
}

.t-medicine__inner {
  position: relative;
  z-index: 190;
  width: rem(666px);
  height: rem(585px);
  margin-left: rem(-46px);
  background-color: transparent;
  background-image: url(../images/t-box.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.t-header__cta {
  padding-top: rem(110px);
  padding-left: rem(80px);

  span {
    display: block;
    color: #ffffff;
    font-family: "Proxima";
    font-size: rem(52px);
    font-weight: 900;
  }

  a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: rem(210px);
    height: rem(56px);
    margin-top: rem(102px);
    box-shadow: 0 rem(12px) rem(13px) rgba(0, 168, 178, 0.13);
    border-radius: rem(28px);
    background-color: #00a8b2;
    color: #ffffff;
    font-family: 'Proxima';
    font-size: rem(12px);
    font-weight: 900;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 1.8px;
    transition: background-color cubic-bezier(0.39, 0.575, 0.565, 1) 0.15s;
    will-change: background-color;

    &:hover {
      color: #fff;
      background-color: #105493;
    }
  }
}