::-webkit-scrollbar {
  width: rem(5px);
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #00a8b2;
  border-radius: rem(10px);  
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: darken($color: #00a8b2, $amount: 15); 
}

.t-container {
  width: 1200px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  height: inherit;
}

.t-main {
  position: relative;
  
  .t-container {
    position: relative;
    z-index: 5;
  }
}

.t-main__decor {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 60%;
  height: 102.4%;
  background-color: #f0f8fe;
}

[data-aos=fade-left] {
  transform: translate(-132%);
}

[data-aos^=fade][data-aos^=fade].aos-animate.t-video__container {
  transform: translate(-32%);
}