*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  min-height: 100%;
}

html {
  font-size: 16px;
}

body {
  font-family: 'Helvetica Neue', sans-serif;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  text-rendering: geometricPrecision;
  font-size: 1rem;
  color: $black;
  margin: 0;
  background: $white;
}

body.overflow {
  overflow: hidden;
}

.hidden {
  display: none !important;
}

h1,
h2,
h3 {
  margin: 0;
}

p {
  margin: 0;
}

button,
input {
  font-family: 'Helvetica Neue', sans-serif;
}

button:focus,
button:active,
input:focus,
input:active {
  outline: none !important;
}
