.t-menu {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 180;
  width: 100%;
  height: 100%;
  background-color: #24b5bf;
}

.t-menu__nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
  width: calc((100% / 12) * 8);
  height: 100%;
  margin-left: calc((100% / 12) * 4);
  padding-right: rem(120px);

  &::after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    background-color: #00a8b2;
  }
}

.t-menu__nav ul {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  z-index: 210;
  height: rem(350px);
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.t-nav__link {
  position: relative;
  color: #fff;
  font-family: 'Proxima';
  font-size: rem(42px);
  font-weight: 900;
  text-decoration: none;

  &:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: rem(2px);
    width: 100%;
    height: rem(2px);
    background-color: #90d9dd;
    transition: background-color cubic-bezier(0.39, 0.575, 0.565, 1) 0.15s;
    will-change: background-color;
  }

  &:hover::after {
    background-color: transparent;
  }
}