.t-header {
  position: relative;
  min-height: rem(900px);

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 60%;
    height: 100%;
    background-color: #eaf2f8;
    background-image: url(../images/t-header__photo.jpg);
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.t-header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: rem(150px);
}

.t-header__logo {
  width: rem(235px);
  height: rem(60px);

  a {
    display: block;
    width: inherit;
    height: inherit;
    background-image: url(../images/t-logo.png);
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.t-header__logo_manufacturer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: rem(210px);
  height: inherit;
  padding-bottom: rem(24px);
  background-color: #005683;

  i {
    width: rem(164px);
    height: rem(54px);
    background-image: url(../images/t-pharma-logo.png);
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.t-header__sandwich {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 200;
  width: rem(36px);
  height: rem(36px);
  margin-top: rem(32px);

  &::before {
    content: '';
    display: block;
    width: rem(36px);
    height: rem(2px);
    background-color: #fff;
    transition: cubic-bezier(0.77, 0, 0.175, 1) 0.3s;
    will-change: transform;
  }

  &::after {
    content: '';
    display: block;
    width: rem(36px);
    height: rem(2px);
    background-color: #fff;
    transform: translateY(5px);
    transition: cubic-bezier(0.77, 0, 0.175, 1) 0.3s;
    will-change: transform;
  }

  &.active {
    &::before {
      transform: rotate(-45deg);
    }

    &::after {
      transform: rotate(45deg) translate(#{rem(-1px)}, #{rem(-1px)});
    }
  }
}