  font-style: normal;
}

@font-face {
  font-family: 'Proxima';
  src: url("../fonts/ProximaNova-Black.woff2") format("woff2"), url("../fonts/ProximaNova-Black.woff") format("woff"), url("../fonts/ProximaNova-Black.svg#ProximaNova-Black") format("svg");
  font-weight: 900;
  font-style: normal;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  min-height: 100%;
}

html {
  font-size: 16px;
}

body {
  font-family: 'Helvetica Neue', sans-serif;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  text-rendering: geometricPrecision;
  font-size: 1rem;
  color: #000000;
  margin: 0;
  background: #ffffff;
}

body.overflow {
  overflow: hidden;
}

.hidden {
  display: none !important;
}

h1,
h2,
h3 {
  margin: 0;
}

p {
  margin: 0;
}

button,
input {
  font-family: 'Helvetica Neue', sans-serif;
}

button:focus,
button:active,
input:focus,
input:active {
  outline: none !important;
}

body::after {
  content: url(../images/t-arrow-right_hover.svg) url(../images/t-video__play-h.svg) url(../images/t-video__pause.svg) url(../images/t-video__sound_hover.png) url(../images/t-video__sound_muted.png) url(../images/t-video__sound_muted_hover.png);
  display: none;
}

::-webkit-scrollbar {
  width: 0.3125rem;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #00a8b2;
  border-radius: 0.625rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #006066;
}

.t-container {
  width: 1200px;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  height: inherit;
}

.t-main {
  position: relative;
}

.t-main .t-container {
  position: relative;
  z-index: 5;
}

.t-main__decor {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 60%;
  height: 102.4%;
  background-color: #f0f8fe;
}

[data-aos=fade-left] {
  transform: translate(-132%);
}

[data-aos^=fade][data-aos^=fade].aos-animate.t-video__container {
  transform: translate(-32%);
}

.t-header {
  position: relative;
  min-height: 56.25rem;
}

.t-header::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  width: 60%;
  height: 100%;
  background-color: #eaf2f8;
  background-image: url(../images/t-header__photo.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.t-header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 9.375rem;
}

.t-header__logo {
  width: 14.6875rem;
  height: 3.75rem;
}

.t-header__logo a {
  display: block;
  width: inherit;
  height: inherit;
  background-image: url(../images/t-logo.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.t-header__logo_manufacturer {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 13.125rem;
  height: inherit;
  padding-bottom: 1.5rem;
  background-color: #005683;
}

.t-header__logo_manufacturer i {
  width: 10.25rem;
  height: 3.375rem;
  background-image: url(../images/t-pharma-logo.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.t-header__sandwich {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 200;
  width: 2.25rem;
  height: 2.25rem;
  margin-top: 2rem;
}

.t-header__sandwich::before {
  content: '';
  display: block;
  width: 2.25rem;
  height: 0.125rem;
  background-color: #fff;
  transition: cubic-bezier(0.77, 0, 0.175, 1) 0.3s;
  will-change: transform;
}

.t-header__sandwich::after {
  content: '';
  display: block;
  width: 2.25rem;
  height: 0.125rem;
  background-color: #fff;
  transform: translateY(5px);
  transition: cubic-bezier(0.77, 0, 0.175, 1) 0.3s;
  will-change: transform;
}

.t-header__sandwich.active::before {
  transform: rotate(-45deg);
}

.t-header__sandwich.active::after {
  transform: rotate(45deg) translate(-0.0625rem, -0.0625rem);
}

.t-menu {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 180;
  width: 100%;
  height: 100%;
  background-color: #24b5bf;
}

.t-menu__nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: relative;
  width: calc((100% / 12) * 8);
  height: 100%;
  margin-left: calc((100% / 12) * 4);
  padding-right: 7.5rem;
}

.t-menu__nav::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  background-color: #00a8b2;
}

.t-menu__nav ul {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  z-index: 210;
  height: 21.875rem;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.t-nav__link {
  position: relative;
  color: #fff;
  font-family: 'Proxima';
  font-size: 2.625rem;
  font-weight: 900;
  text-decoration: none;
}

.t-nav__link:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0.125rem;
  width: 100%;
  height: 0.125rem;
  background-color: #90d9dd;
  transition: background-color cubic-bezier(0.39, 0.575, 0.565, 1) 0.15s;
  will-change: background-color;
}

.t-nav__link:hover::after {
  background-color: transparent;
}

.t-header__presentation {
  display: flex;
  margin-top: 5.625rem;
}

.t-medicine__inner {
  position: relative;
  z-index: 190;
  width: 41.625rem;
  height: 36.5625rem;
  margin-left: -2.875rem;
  background-color: transparent;
  background-image: url(../images/t-box.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.t-header__cta {
  padding-top: 6.875rem;
  padding-left: 5rem;
}

.t-header__cta span {
  display: block;
  color: #ffffff;
  font-family: "Proxima";
  font-size: 3.25rem;
  font-weight: 900;
}

.t-header__cta a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 13.125rem;
  height: 3.5rem;
  margin-top: 6.375rem;
  box-shadow: 0 0.75rem 0.8125rem rgba(0, 168, 178, 0.13);
  border-radius: 1.75rem;
  background-color: #00a8b2;
  color: #ffffff;
  font-family: 'Proxima';
  font-size: 0.75rem;
  font-weight: 900;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 1.8px;
  transition: background-color cubic-bezier(0.39, 0.575, 0.565, 1) 0.15s;
  will-change: background-color;
}

.t-header__cta a:hover {
  color: #fff;
  background-color: #105493;
}

.t-main__use {
  padding-top: 7.1875rem;
  padding-bottom: 7.1875rem;
}

.t-use__article {
  display: flex;
  justify-content: space-between;
}

.t-use__article .t-use__text {
  width: calc(100% - 30rem);
}

.t-use__article .t-use__text span {
  margin-bottom: 3.6875rem;
  color: #105493;
  font-family: 'Proxima';
  font-size: 2.625rem;
  font-weight: 900;
}

.t-use__article .t-use__text small {
  display: block;
  margin-top: 3.6875rem;
  margin-bottom: 3.25rem;
  color: #979797;
  font-family: 'Proxima';
  font-size: 0.75rem;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 0.1125rem;
  line-height: 0;
}

.t-use__article .t-use__text div {
  margin-top: 3.25rem;
  color: #333;
  font-size: 1rem;
  line-height: 1.375rem;
}

.t-use__article .t-use__illustration {
  width: 24.125rem;
  height: 32.875rem;
  background-color: #eaf2f8;
  background-image: url(../images/t-use__photo--first.jpg);
  background-size: contain;
  background-repeat: no-repeat;
}

.t-use__article_paired .t-use__text {
  width: calc(100% - 37.875rem);
}

.t-use__article_paired .t-use__text div {
  margin-top: 6.25rem;
  margin-bottom: 3.625rem;
}

.t-use__article_paired .t-use__text a {
  color: #00a8b2;
  font-family: 'Proxima';
  font-size: 0.75rem;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 0.1125rem;
  text-decoration: none;
  transition: color cubic-bezier(0.47, 0, 0.745, 0.715) 0.9ms;
  will-change: color;
}

.t-use__article_paired .t-use__text a span {
  margin-bottom: 0;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
}

.t-use__article_paired .t-use__text a i {
  display: inline-block;
  width: 0.6875rem;
  height: 0.6875rem;
  background-image: url(../images/t-arrow-right.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.t-use__article_paired .t-use__text a:hover {
  color: #105493;
}

.t-use__article_paired .t-use__text a:hover i {
  background-image: url(../images/t-arrow-right--hover.svg);
}

.t-use__article_paired .t-use__illustration {
  order: -1;
  width: 30.6875rem;
  height: 28.9375rem;
  background-image: url(../images/t-use__photo--second.jpg);
}

.t-main__video {
  position: relative;
}

.t-video__container {
  position: relative;
  width: 73.125rem;
  height: 47.5rem;
}

.t-video__container .t-video__preview {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../images/t-video__preview.jpg);
  background-size: contain;
  background-repeat: no-repeat;
  filter: grayscale(100%);
}

.t-video__container .t-video__player {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  filter: none;
}

.t-video__container .t-video__player:hover {
  cursor: pointer;
}

.t-video__container.active:hover .t-video__pause {
  display: block;
}

.t-video__play {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  border: none;
  background-color: transparent;
  transform: translate(-50%, -50%);
}

.t-video__play .t-play__icon {
  display: block;
  width: 2.3125rem;
  height: 2.625rem;
  margin-bottom: 2.5rem;
  background-image: url(../images/t-video__play.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.t-video__play .t-play__descr {
  color: #fff;
  font-size: 1rem;
  line-height: 1.375rem;
}

.t-video__play:hover {
  cursor: pointer;
}

.t-video__play:hover .t-play__icon {
  background-image: url(../images/t-video__play-h.svg);
}

.t-video__play:hover .t-play__descr {
  color: #8e9295;
}

.t-video__pause {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 3;
  width: 1.5rem;
  height: 2.375rem;
  border: none;
  background-color: transparent;
  background-image: url(../images/t-video__pause.svg);
  transform: translate(-50%, -50%);
}

.t-video__pause:hover {
  cursor: pointer;
  background-image: url(../images/t-video__pause-h.svg);
}

.t-video__sound {
  display: none;
  position: absolute;
  bottom: 4rem;
  left: 12.5rem;
  z-index: 3;
  width: 2.125rem;
  height: 2.125rem;
  border: none;
  background-color: transparent;
  background-image: url(../images/t-video__sound.png);
}

.t-video__sound:hover {
  cursor: pointer;
  background-image: url(../images/t-video__sound_hover.png);
}

.t-video__sound.muted {
  width: 1.3125rem;
  background-image: url(../images/t-video__sound_muted.png);
}

.t-video__sound.muted:hover {
  background-image: url(../images/t-video__sound_muted_hover.png);
}

.t-video__descr {
  position: absolute;
  top: 0;
  right: -3.125rem;
  width: 31.25rem;
  padding-top: 6.75rem;
}

.t-video__descr span {
  color: #105493;
  font-family: 'Proxima';
  font-size: 2.625rem;
}

.t-video__descr a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 13.125rem;
  height: 3.5rem;
  margin-top: 5rem;
  box-shadow: 0 0.75rem 0.8125rem rgba(0, 168, 178, 0.13);
  border-radius: 1.75rem;
  background-color: #00a8b2;
  color: #ffffff;
  font-family: 'Proxima';
  font-size: 0.75rem;
  font-weight: 900;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.1125rem;
  transition: background-color cubic-bezier(0.39, 0.575, 0.565, 1) 0.15s;
  will-change: background-color;
}

.t-video__descr a:hover {
  color: #fff;
  background-color: #105493;
  text-decoration: none;
}

.t-footer {
  height: 13.75rem;
}

.t-footer .t-container {
  display: flex;
  align-items: center;
}

.t-footer__copy {
  border-bottom: 0.0625rem solid #00a8b2;
  padding-bottom: 0.375rem;
  color: #979797;
  font-size: 1rem;
  line-height: 1.375rem;
}
