.t-main__use {
  padding-top: rem(115px);
  padding-bottom: rem(115px);
}

.t-use__article {
  display: flex;
  justify-content: space-between;

  .t-use__text {
    width: calc(100% - #{rem(480px)});

    span {
      margin-bottom: rem(59px);
      color: #105493;
      font-family: 'Proxima';
      font-size: rem(42px);
      font-weight: 900;
    }

    small {
      display: block;
      margin-top: rem(59px);
      margin-bottom: rem(52px);
      color: #979797;
      font-family: 'Proxima';
      font-size: rem(12px);
      font-weight: 900;
      text-transform: uppercase;
      letter-spacing: rem(1.8px);
      line-height: 0;
    }

    div {
      margin-top: rem(52px);
      color: #333;
      font-size: rem(16px);
      line-height: rem(22px);
    }
  }

  .t-use__illustration {
    width: rem(386px);
    height: rem(526px);
    background-color: #eaf2f8;
    background-image: url(../images/t-use__photo--first.jpg);
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.t-use__article_paired {

  .t-use__text {
    width: calc(100% - #{rem(606px)});

    div {
      margin-top: rem(100px);
      margin-bottom: rem(58px);
    }

    a {
      color: #00a8b2;
      font-family: 'Proxima';
      font-size: rem(12px);
      font-weight: 900;
      text-transform: uppercase;
      letter-spacing: rem(1.8px);
      text-decoration: none;
      transition: color cubic-bezier(0.47, 0, 0.745, 0.715) 0.9ms;
      will-change: color;

      span {
        margin-bottom: 0;
        color: inherit;
        font-family: inherit;
        font-size: inherit;
      }

      i {
        display: inline-block;
        width: rem(11px);
        height: rem(11px);
        background-image: url(../images/t-arrow-right.svg);
        background-size: contain;
        background-repeat: no-repeat;
      }

      &:hover {  
        color: #105493;

        i {
          background-image: url(../images/t-arrow-right--hover.svg);
        }
      }
    }
  }

  .t-use__illustration {
    order: -1;
    width: rem(491px);
    height: rem(463px);
    background-image: url(../images/t-use__photo--second.jpg);
  }
}